































































import { Component, Vue } from "vue-property-decorator";
import { settings } from "@/data/settings";

export class FormModel {
  action = "";
  sound = false;
  reminingTime = 100;
}

@Component
export default class Settings extends Vue {
  model = new FormModel();

  constructor() {
    super();
    this.model.action = settings.actionAtEnd;
    this.model.sound = settings.soundOn;
    this.model.reminingTime = settings.timeRemaning;
  }

  submit(): void {
    console.info("settings", this.model);

    settings.setTimeout(this.model.reminingTime);
    settings.setActionAtEnd(this.model.action);
    settings.setSoundOn(this.model.sound);
    this.$router.push("/");
    // settings.setTimesPerMinute(this.model.get(this.TIMES_PER_MINUTE_INPUT).value);
    // settings.setAccentBetweenTicks(this.model.get(this.ACCENT_BETWEEN_TICKS_INPUT).value);
  }
}
